// src/app/page.tsx
'use client';

import { SearchBar, MovieList } from '@/components/ui/movie-components';
import { useMovieSearch } from '';

export default function Home() {
  const { search, isSearching, data } = useMovieSearch();

  return (
    <main className="min-h-screen p-4 md:p-8 bg-background">
      <div className="max-w-7xl mx-auto space-y-8">
        <div className="text-center space-y-4">
          <h1 className="text-4xl font-bold tracking-tight">Movie RAG</h1>
          <p className="text-muted-foreground">
            Ask anything about movies and get intelligent recommendations
          </p>
        </div>
        
        <SearchBar onSearch={search} isLoading={isSearching} />
        <MovieList data={data} isLoading={isSearching} />
      </div>
    </main>
  );
}

// src/hooks/use-movie-search.ts
import { useMutation, useQueryClient } from '@tanstack/react-query';
import { searchMovies } from '@/lib/api';

export function useMovieSearch() {
  const queryClient = useQueryClient();
  
  const { mutate: search, isPending: isSearching } = useMutation({
    mutationFn: searchMovies,
    onSuccess: (data) => {
      queryClient.setQueryData(['movieSearch'], data);
    },
  });

  const data = queryClient.getQueryData(['movieSearch']);

  return {
    search,
    isSearching,
    data,
  };
}